import React from "react";
import HomePresenter from "./HomePresenter";
import { moviesApi } from "api";

export default class extends React.Component {
  // componentWillMount() {
  //   console.log("componentWillMount í™•ì¸");
  // }
  state = { nowPlaying: null, upcoming: null, popular: null, error: null, loading: true };
  async componentDidMount() {
    try {
      const {
        data: { results: nowPlaying }, //ë’¤ì— nowPlayingì´ê±°ëŠ” ì´ë¦„ ë³€ìˆ˜ëª… ì§€ì •.
      } = await moviesApi.nowPlaying();
      const {
        data: { results: upcoming },
      } = await moviesApi.upcoming();
      const {
        data: { results: popular },
      } = await moviesApi.popular();
      this.setState({ nowPlaying, upcoming, popular }); //nowPlaying:nowPlaying
      // const nowPlaying = await moviesApi.nowPlaying();
      // console.log(nowPlaying);
      //ìœ„ì˜ ì˜ˆëŠ” moviesApi.jsì˜ nowPlayingë¥¼ ë¶ˆëŸ¬ì™€ì„œ dataì•ˆì˜ results ë°ì´í„°ë¥¼ nowPlayingì´ë¦„ìœ¼ë¡œ í™œìš©
    } catch (error) {
      this.setState({ error: "Can't find Movie information" });
    } finally {
      this.setState({ loading: false });
    }
  }
  render() {
    const { nowPlaying, upcoming, popular, error, loading } = this.state;
    //console.log(this.state);
    return (
      <HomePresenter
        nowPlaying={nowPlaying}
        upcoming={upcoming}
        popular={popular}
        error={error}
        loading={loading}
      />
    );
  }
}

