import React from "react";
import DetailPresenter from "./DetailPresenter";
import { moviesApi, tvApi } from "../../api";

export default class extends React.Component {
  constructor(props) {
    super(props);
    const {
      location: { pathname },
    } = props;
    this.state = {
      result: null,
      error: null,
      loading: true,
      isMovie: pathname.includes("/movie/"), //ì£¼ì†Œì— /movieê°€ ìˆìœ¼ë©´ true ì—†ìœ¼ë©´ false
    };
  }

  async componentDidMount() {
    const {
      match: {
        params: { id },
      },
      history: { push },
    } = this.props; //historyì•ˆì— pushê¸°ëŠ¥ì´ ë³´ë‚´ëŠ”ê¸°ëŠ¥ ë°‘ì— 29ë²ˆ
    const { isMovie } = this.state;
    const parsedId = parseInt(id); //idê°€ stringì¸ê±¸ numberë¡œ ë°”ê¾¸ì–´ì¤Œ.
    if (isNaN(parsedId)) {
      return push("/"); //homeí™”ë©´ìœ¼ë¡œ êº¼ì§€ë¼ëŠ”ê±°
    }
    let result = null;
    try {
      if (isMovie) {
        ({ data: result } = await moviesApi.movieDetail(parsedId));
        //const { data: result } ì´ê±°ë‘ ê°™ì€ê±°
      } else {
        ({ data: result } = await tvApi.showDetail(parsedId));
      }
    } catch {
      this.setState({ error: "Can't find anything." });
    } finally {
      this.setState({ loading: false, result });
    }
  }

  render() {
    const { result, error, loading } = this.state;
    return <DetailPresenter result={result} error={error} loading={loading} />;
  }
}
